/* Heads up!
 * Neither Semantic UI nor Semantic UI React offer a responsive navbar, however, it can be implemented easily.
 * It can be more complicated, but you can create really flexible markup.
 */
class DesktopContainer extends Component {
  state = {}

  hideFixedMenu = () => this.setState({ fixed: false })
  showFixedMenu = () => this.setState({ fixed: true })

  render() {
    const { children } = this.props
    const { fixed } = this.state

    return (
      <Responsive getWidth={getWidth} minWidth={Responsive.onlyTablet.minWidth}>
        <Visibility
          once={false}
          onBottomPassed={this.showFixedMenu}
          onBottomPassedReverse={this.hideFixedMenu}
        >
          <Segment
            inverted
            textAlign='center'
            style={{ minHeight: 500, padding: '1em 0em', backgroundColor: '#2e1b4c'}}
            vertical
          >
     
            <Menu 
              fixed={'top'}
              inverted={true}
              pointing={true}
              secondary={true}
              style={{backgroundColor: '#2e1b4c'}}
              size='large'
            >
              <Container>
                <Menu.Item as='a' active href='#top'>
                  IEEE Western Branch
                </Menu.Item>
                <Menu.Item as='a' href="#about">About</Menu.Item>
                {/* <Menu.Item as='a' href='https://western.ieee.ca/store' target="_blank">Store</Menu.Item> */}
                <Menu.Item as='a' href='https://western.ieee.ca/rentals' target="_blank">Rentals</Menu.Item>
                <Menu.Item position='right'>
                  <Button as='a' 
                      href='https://www.ieee.org/membership/join/index.html'
                      target="_blank"
                      inverted={!fixed} 
                      primary={fixed} 
                      style={{ marginLeft: '0.5em' }}>
                    Join IEEE
                  </Button>
                </Menu.Item>
              </Container>
            </Menu>

        
            <HomepageHeading style={{overflow:'hidden'}}/>
             
          </Segment>
        </Visibility>

        {children}
      </Responsive>
    )
  }
}

DesktopContainer.propTypes = {
  children: PropTypes.node,
}

class MobileContainer extends Component {
  state = {}

  handleSidebarHide = () => this.setState({ sidebarOpened: false })

  handleToggle = () => this.setState({ sidebarOpened: true })

  render() {
    const { children } = this.props
    const { sidebarOpened } = this.state

    return (
      <Responsive
        as={Sidebar.Pushable}
        getWidth={getWidth}
        maxWidth={Responsive.onlyMobile.maxWidth}
      >
        <Sidebar
          as={Menu}
          animation='push'
          inverted
          onHide={this.handleSidebarHide}
          vertical
          visible={sidebarOpened}
        >
          <Menu.Item as='a' active >
            Home
          </Menu.Item>
          <Menu.Item as='a' href="#about">About</Menu.Item>
          {/*<Menu.Item as='a' href='https://western.ieee.ca/store/' target="_blank" >Store</Menu.Item>*/}
          <Menu.Item as='a' href='https://western.ieee.ca/rentals/' target="_blank">Rentals</Menu.Item>
        </Sidebar>

        <Sidebar.Pusher dimmed={sidebarOpened}>
          <Segment
            inverted
            textAlign='center'
            style={{ minHeight: 350, padding: '1em 0em', backgroundColor: '#2e1b4c', zIndex: -1}}
            vertical
          >
            <Container>
              <Menu fixed={'top'} inverted pointing secondary size='large' >
                <Menu.Item onClick={this.handleToggle}>
                  <Icon name='sidebar' />
                </Menu.Item>
                <Menu.Item position='right'>
                  <Button as='a' inverted
                      href='https://www.ieee.org/membership/join/index.html'
                      target="_blank">
                    Join IEEE
                  </Button>
                  
                </Menu.Item>
              </Menu>
            </Container>
            <HomepageHeading mobile />
          </Segment>

          {children}
        </Sidebar.Pusher>
      </Responsive>
    )
  }
}

MobileContainer.propTypes = {
  children: PropTypes.node,
}

const ResponsiveContainer = ({ children }) => (
  <div>
    <DesktopContainer>{children}</DesktopContainer>
    <MobileContainer>{children}</MobileContainer>
  </div>
)

ResponsiveContainer.propTypes = {
  children: PropTypes.node,
}

